import Logo from "../assets/wwbm.png";
import { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import LifeTime from "./Lifetime";
import "../styles/game.css";
import CustomQuestions from "./customQuestions";
import { faVolumeMute, faVolumeUp } from "@fortawesome/free-solid-svg-icons";

function Game() {
   const money = [
      "$ 1 000 000",
      "$ 500 000",
      "$ 250 000",
      "$ 64 000",
      "$ 32 000",
      "$ 16 000",
      "$ 8 000",
      "$ 4 000",
      "$ 2 000",
      "$ 1 000",
      "$ 500",
      "$ 300",
      "$ 200",
      "$ 100",
   ];

   const [data, setdata] = useState([]);
   const [currentQuestion, setCurrentQuestion] = useState(0);
   const [moneyLadder, setMoneyLadder] = useState(15);
   const [moneyLadderMobile, setmoneyLadderMobile] = useState(false);
   const [sound, setSound] = useState();

   const handleSound = () => {
      setSound((sound) => {
         return !sound;
      });
   };
   const shuffle = (arr) => arr.sort(() => Math.random() - 0.5);
   const questionsArr = [];
   let results;
   useEffect(() => {
      const fetchData = () => {
         return fetch(
            "https://opentdb.com/api.php?amount=50&category=9&easy=medium&type=multiple"
         )
            .then((response) => response.json())
            .then((data) => {
               results = data.results;
               results.forEach((item) => {
                  let inCorrect = [...item.incorrect_answers];
                  let questions = {
                     question: item.question,
                     options: shuffle([...inCorrect, item.correct_answer]),
                     answer: item.correct_answer,
                  };
                  questionsArr.push(questions);
               });

               setdata(shuffle(questionsArr));
            });
      };
      fetchData();
   }, []);

   return (
      <>
         <div className="gameMain">
            {data[currentQuestion] && (
               <div className="gameSub">
                  <div className="QandA">
                     <div className="holder">
                        <div className="logoDiv">
                           <img src={Logo} alt="logo" width="200" />
                        </div>
                        {sound ? (
                           <FontAwesomeIcon
                              className="sound"
                              icon={faVolumeMute}
                              onClick={handleSound}
                              size="lg"
                           />
                        ) : (
                           <FontAwesomeIcon
                              className="sound"
                              icon={faVolumeUp}
                              onClick={handleSound}
                              size="lg"
                           />
                        )}
                        <div className="lifeTime">
                           <LifeTime
                              data={data}
                              currentQuestion={currentQuestion}
                           />
                        </div>
                     </div>

                     <div className="questionDiv">
                        <div>
                           <div className="mt-5">
                              <div className="question">
                                 <h5
                                    dangerouslySetInnerHTML={{
                                       __html: data[currentQuestion].question,
                                    }}
                                 />
                              </div>
                           </div>
                           <div className="answers">
                              {data[currentQuestion].options.map(
                                 (option, index) => (
                                    <CustomQuestions
                                       data={data}
                                       option={option}
                                       index={index}
                                       setMoneyLadder={setMoneyLadder}
                                       moneyLadder={moneyLadder}
                                       setmoneyLadderMobile={
                                          setmoneyLadderMobile
                                       }
                                       setCurrentQuestion={setCurrentQuestion}
                                       currentQuestion={currentQuestion}
                                    />
                                 )
                              )}
                           </div>
                        </div>
                        {currentQuestion === 14 &&
                           (alert(
                              "Congratulations!! You just won 1 million naira"
                           ),
                           setCurrentQuestion(0),
                           data[currentQuestion],
                           setMoneyLadder(15),
                           window.location.reload())}
                     </div>
                  </div>

                  <div className="money">
                     <div>
                        {money.map((money, index) => (
                           <h5
                              key={index}
                              className={
                                 moneyLadder === index + 1
                                    ? "bg-warning text-white p-1"
                                    : ""
                              }
                           >
                              {money}
                           </h5>
                        ))}
                     </div>
                  </div>
               </div>
            )}
            {moneyLadderMobile === true && (
               <div className="moneyMobile">
                  {money.map((money, index) => (
                     <h5
                        key={index}
                        className={
                           moneyLadder === index + 1
                              ? "bg-warning text-white p-1"
                              : ""
                        }
                     >
                        {money}
                     </h5>
                  ))}
               </div>
            )}
         </div>
      </>
   );
}

export default Game;
