import { useState } from "react";
import useSound from "use-sound";
import correctAnswer from "../sounds/correctAnswer.mp3";
import wrongAnswer from "../sounds/wrongAnswer.mp3";
import finalAnswer from "../sounds/finalAnswer.mp3";
import countDown from "../sounds/countdown.mp3";

function CustomQuestions({
   option,
   setmoneyLadderMobile,
   data,
   setMoneyLadder,
   setCurrentQuestion,
   currentQuestion,
   index,
   moneyLadder,
}) {
   const [play, { stop }] = useSound(countDown);
   const [play1] = useSound(correctAnswer);
   const [play2] = useSound(wrongAnswer);
   const [play3] = useSound(finalAnswer);

   // const [play] = useSound("../sounds/sprite.mp3", {
   //    sprite: {
   //       countdown: [0, 350],
   //       finalAnswer: [374, 160],
   //       correctAnswer: [666, 290],
   //       wrongAnswer: [968, 200],
   //    },
   // });

   const [changeColor, setChangeColor] = useState("");
   const handleClick = () => {
      const nextQuestion = currentQuestion + 1;
      stop();
      play3();
      if (data[currentQuestion].answer === option) {
         setTimeout(() => {
            play1();
            setChangeColor("success text-white");
         }, 3000);
         setTimeout(() => {
            setmoneyLadderMobile(true);
            setMoneyLadder(moneyLadder - 1);
         }, 6000);
         setTimeout(() => {
            // pause();
            setCurrentQuestion(nextQuestion);
            setmoneyLadderMobile(false);
            setChangeColor("");
         }, 8000);
      } else {
         setTimeout(() => {
            play2();
            setChangeColor("danger");
         }, 3000);
         setTimeout(() => {
            window.location.reload();
            setCurrentQuestion(0);
            setMoneyLadder(15);
         }, 6000);
      }
      play();
   };

   return (
      <>
         <button
            className={`bg-${changeColor}`}
            // ${data[currentQuestion].answer === option ? "text-success" : ""}
            key={index}
            onClick={() => handleClick(option, index)}
            dangerouslySetInnerHTML={{
               __html: `${index + 1})  ${option}`,
            }}
         ></button>
      </>
   );
}

export default CustomQuestions;
