import { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button, Container, Row, Col, Modal } from "react-bootstrap";
import { TelephoneFill } from "react-bootstrap-icons";
import { faUsers } from "@fortawesome/free-solid-svg-icons";

function LifeTime({ data, currentQuestion }) {
   const fiftyFifty = () => {
      data[currentQuestion].options.splice(0, 2);
   };

   const [className, setclassName] = useState("");
   //    const [disable, setDisable] = useState("");
   const [show, setShow] = useState(false);
   const [show2, setShow2] = useState(false);
   //    const [height, setHeight] = useState();
   //    const [inCorrect, setinCorrect] = useState();

   const callFriendClose = () => setShow(false);
   const callFriendShow = () => {
      setTimeout(() => {
         setShow(true);
      }, 2000);
      setclassName("disabled");
   };
   const askAudienceShow = () => {
      setTimeout(() => {
         setShow2(true);
      }, 2000);
      //   setDisable("disabled");
   };
   const askAudienceClose = () => setShow2(false);

   //    const randomNumber = () => {
   //       data[currentQuestion].options.map((option) => {
   //          if (data[currentQuestion].answer === option) {
   //             setHeight(Math.floor(Math.random() * (100 - 80) + 80));
   //          }
   //       });
   //       randomNumber();
   //    };

   return (
      <>
         <Button className="" onClick={fiftyFifty}>
            50/50
         </Button>

         <Button disabled={className} onClick={callFriendShow}>
            <TelephoneFill />
         </Button>
         <Modal
            show={show}
            onHide={callFriendClose}
            backdrop="static"
            keyboard={false}
            centered
         >
            <Modal.Body className="callAfriend">
               <h5
                  className="mb-5"
                  dangerouslySetInnerHTML={{
                     __html: `Hello Friend!! The right answer is "${data[currentQuestion].answer}"`,
                  }}
               />
               <Button
                  style={{
                     float: "right",
                     color: "black",
                     backgroundColor: "rgb(255, 166, 0)",
                  }}
                  onClick={callFriendClose}
               >
                  Close
               </Button>
            </Modal.Body>
         </Modal>

         <Button onClick={askAudienceShow}>
            <FontAwesomeIcon icon={faUsers} />
         </Button>
         <Modal
            show={show2}
            onHide={askAudienceClose}
            backdrop="static"
            keyboard={false}
            centered
         >
            <Modal.Body className="callAfriend">
               <Container className="mb-3">
                  <Row>
                     <Col>
                        <div></div>
                        <h5></h5>
                        <h5>1</h5>
                     </Col>
                     <Col>
                        <div></div>
                        <h5></h5>
                        <h5>2</h5>
                     </Col>
                     <Col>
                        <div></div>
                        <h5></h5>
                        <h5>3</h5>
                     </Col>
                     <Col>
                        <div></div>
                        <h5></h5>
                        <h5>4</h5>
                     </Col>
                  </Row>
               </Container>
               <Button
                  style={{
                     float: "right",
                     color: "black",
                     backgroundColor: "rgb(255, 166, 0)",
                  }}
                  onClick={askAudienceClose}
               >
                  Close
               </Button>
            </Modal.Body>
         </Modal>
      </>
   );
}

export default LifeTime;
